
import { Options, Vue } from "vue-class-component";

@Options({
  components: {},
})
export default class About extends Vue {
  keyValueText: String = "sss";
  jsonText: String = "sss";

  mounted() {}

  jsonToKeyValue() {
    this.keyValueText = this.jsonText;
  }
  keyValueToJson() {
    this.jsonText = this.keyValueText;
  }
}
